@charset "utf-8";
// Normalize default styles and browser inconsistencies. see https://necolas.github.io/normalize.css/
@import "normalize";

// Import Bourbon, Neat & Bitters
@import "bourbon";
@import "grid-settings";
@import "neat";
@import "base/base";

// Import site specific CSS

body {
  font-family: "Lucida Grande", "Tahoma", "Verdana", "Arial", sans-serif;
}

.links {
  a {
    text-decoration: underline;
    color: #555;
    margin-left: 0.5em;
    margin-right: 0.5em;
  }
}

.main {
  width: 600px;
  margin: 0 auto;
}

.head {
  margin: 0 auto;
  width: 100%;
  padding-bottom: 1.8em;
  p, h1 {
    text-align: center;
  }
}
iframe{
  margin: 0 auto;
  display:block;
}


em {
  text-decoration:underline;
}

.content {
  width: 440px;
  margin: 0 auto;
  padding-top: 3em;

}
.procrastinator-content {
  a {
    color: gray;
  }
  li {
    list-style: square;
  }
}

li {
  padding-top: 1em;
}

h2 {
  font-size: 1.2em;
  text-align: center;
}
